.homeContainer {
    display: flex;
    flex-direction: row;
    height: 100vh;
    justify-content: space-evenly;
}

.periodicTableContainer {
    align-items: center;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    justify-content: space-evenly;
}

.title {
    font-size: 5rem;
    font-weight: 100;
    white-space: nowrap;
}

.elementsContainer {
    column-gap: calc(1rem / 2);
    display: grid;
    grid-template-columns: 18;
    grid-template-rows: 7;
    row-gap: calc(1rem / 2);
}

.columnLabel, .rowLabel {
    align-items: center;
    display: flex;
    justify-content: center;
}

.columnLabel {
    width: 3.125rem;
}

.rowLabel {
    height: 3.125rem;
}

.element {
    align-items: center;
    border: 1px solid;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    height: 3.125rem;
    justify-content: center;
    width: 3.125rem;
}

.elementSymbol {
    font-size: 1.25rem;
    font-weight: 600;
}

.floatingMenuContainer {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    left: 0;
    padding: 1rem;
}

.darkModeButton {
    border-radius: 50%;
    border-style: solid;
    border-width: 1px;
    font-size: 0;
    padding: 1rem;
}

.darkModeButton img {
    height: 1rem;
    width: 1rem;
}

@media(min-width: 0px) and (max-width: 575px) {
    html, .homeContainer, .periodicTableContainer {
        overflow-x: hidden;
    }
    
    .homeContainer {
        flex-direction: column;
    }

    .periodicTableContainer {
        align-items: initial;
    }

    .title {
        text-align: center;
    }

    .elementsContainer {
        overflow-x: scroll;
    }
}

@media(min-width: 576px) and (max-width: 767px) {
    html, .homeContainer, .periodicTableContainer {
        overflow-x: hidden;
    }

    .homeContainer {
        justify-content: initial;
    }

    .periodicTableContainer {
        align-items: initial;
        justify-content: initial;
    }

    .elementsContainer {
        overflow: scroll;
    }
}

@media(min-width: 768px) and (max-width: 991px) {
    html, .homeContainer, .periodicTableContainer {
        overflow-x: hidden;
    }

    .homeContainer {
        flex-direction: column;
        justify-content: initial;
    }

    .periodicTableContainer {
        align-items: initial;
    }

    .title {
        text-align: center;
    }

    .elementsContainer {
        overflow: scroll;
    }
}

@media(min-width: 992px) and (max-width: 1200px) {
    .homeContainer {
        flex-direction: column;
    }

    .periodicTableContainer {
        align-items: initial;
        justify-content: initial;
    }

    .title {
        text-align: center;
    }
}
